// ++COLORS

$base-colors: (
  // "bg": #fff,
  // "text": #333,
  // "accent1": #8394f4,
  // "accent2": #2bd480,
  // "accent3" : coral,
  "bg": #f4f4f0,
  "text": #333,
  "accent1": #8394f4,
  "accent2": #2fc3a6,
  "accent3" : #ff9500,
);

$color-variations: (
  "xxlight": 30%,
  "xlight": 20%,
  "light": 10%,
  "normal": 0%,
  "dark": -3%,
  "xdark": -6%,
  "xxdark": -9%
);

@function get-color($name, $variation: null) {
  $color-value: map-get($base-colors, $name);

  // Si se especifica una variación, ajustar el color
  @if $variation {
    $variation-amount: map-get($color-variations, $variation);
    $adjusted-color: if($variation-amount >=0, lighten($color-value, $variation-amount), darken($color-value, abs($variation-amount)));
    @return $adjusted-color;
  }

  @return $color-value;
}

// ++MIXINS
@mixin darkMode {

  // @if($darkMode) {
  @media(prefers-color-scheme: dark) {
    @content;
  }

  // }
}

@mixin bg-color($name, $variation: null) {
  background-color: get-color($name, $variation);
}

@mixin text-color($name, $variation: null) {
  color: get-color($name, $variation);
}

@mixin outline($color, $variation: null, $width: 0.1rem) {
  $final-color: get-color($color, $variation);

  outline-color: $final-color;
  outline-width: $width;
  outline-style: solid;
}





:root {

  @each $color-name,
  $color-value in $base-colors {

    @each $variation-name,
    $variation-amount in $color-variations {
      $property-name: "#{$color-name}-#{$variation-name}";
      $final-color: get-color($color-name, $variation-name);

      #{$property-name} {
        color: $final-color;
      }
    }
  }
}





