// Generar clases de background-color
@each $color-name,
$color-value in $base-colors {

  @each $variation-name,
  $variation-amount in $color-variations {
    $class-name: "#{$color-name}-#{$variation-name}";

    .#{$class-name} {
      background-color: get-color($color-name, $variation-name);
    }
  }
}


